.expertise-main-container {
  position: relative;
  width: 100%;
  height: auto;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.expertise-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 0.4;
}

.expertise-title {
  position: relative;
  color: white;
  font-size: 50px;
  text-transform: uppercase;
  margin: 200px 0 0 0;
  letter-spacing: 3px;
}

.expertise-main-content {
  position: relative;
  margin: 170px 0 12% 0;
  height: auto;
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

.expertise-content {
  height: calc(100vw / 6);
  width: calc(100vw / 6);
  margin: -1px;
  background-color: rgba(225, 225, 225, 0.9);
  color: #161616;
  font-size: 27px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition-duration: .15s;
  text-align: center;
}
.expertise-content:hover {
  background-color: rgba(255, 255, 255, 1);
}

@media screen and (max-width: 1200px) {

  .expertise-content {
    font-size: 20px;
  }

}

@media screen and (max-width: 920px) {

  .expertise-content {
    height: calc(100vw / 3);
    width: calc(100vw / 3);
    font-size: 20px;
    margin-bottom: 50px;
  }

}

@media screen and (max-width: 500px) {

  .expertise-content {
    height: calc(100vw - 100px);
    width: calc(100vw - 100px);
    font-size: 30px;
  }

}


