.tourism-main-container {
  position: relative;
  width: 100%;
  height: auto;
  background-color: #161616;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tourism-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 0.4;
}

.tourism-main-backfill {
  position: relative;
  height: 50%;
  width: 100%;
  background-color: #161616;
}

.tourism-main-content {
  position: relative;
  width: 85%;
  display: flex;
  flex-direction: column;
  height: auto;
  margin: 200px 0 0 0;
}

.tourism-title {
  position: relative;
  height: 165px;
  width: 100%;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  text-transform: uppercase;
  color: white;
  font-size: 50px;
  margin: 0 0 0 0;
}

.tourism-experts {
  position: relative;
  background-color: white;
  height: auto;
}

.tourism-experts-top {
  position: relative;
  height: 15px;
  width: 100%;
  display: flex;
  flex-direction: row;
}
.tourism-experts-top-left {
  height: 100%;
  width: 30%;
  background-color: #BD9A35;
}
.tourism-experts-top-right {
  height: 100%;
  width: 70%;
  background-color: #D0D0D0;
}
.tourism-industry-experts {
  position: absolute;
  top: 120px;
  right: 70%;
  font-size: 35px;
  color: #BD9A35;
  text-transform: uppercase;
  text-align: right;
  letter-spacing: 3px;
  line-height: 35px;
  font-family: 'Rogan-SemiBold', sans-serif;
}
.tourism-industry-info {
  position: relative;
  left: 35%;
  color: #727272;
  width: 60%;
  height: auto;
  font-family: 'Rogan-Light', sans-serif;
  font-size: 2vmin;
  letter-spacing: 1px;
  line-height: 32px;
  margin: 100px 0 100px 0;
}

.tourism-video {
  position: relative;
  height: auto;
  width: 100%;
  background-color: transparent;
}
