.tourism-video-underlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: black;
  opacity: 0.8;
}
.tourism-tuac-video {
  position: relative;
  width: 85%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0%);
  margin: 100px 0 100px 0;
}

.tourism-clients-title {
  position: relative;
  height: 160px;
  width: 100%;
  background-color: white;
  color: #BD9A35;
  text-transform: capitalize;
  font-size: 28px;
  letter-spacing: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.tourism-clients {
  position: relative;
  height: auto;
  width: 100%;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tourism-clients-clients {
  position: relative;
  height: calc(12vw * 2);
  width: calc(12vw * 4.1);
  cursor: pointer;
  margin: 100px 0 100px 0;
}
.tourism-clients-clients img {
  margin: -3px;
  outline: 0;
  padding: 0;
}

.tourism-work {
  height: 12vw;
  margin: 0;
  outline: 0;
}

.tourism-awards-title {
  position: relative;
  height: 160px;
  width: 100%;
  background-color: white;
  color: #BD9A35;
  text-transform: capitalize;
  font-size: 28px;
  letter-spacing: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.tourism-awards {
  position: relative;
  height: auto;
  width: 100%;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tourism-awards-awards {
  position: relative;
  height: auto;
  width: 90%;
  color: white;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 75px 0 75px 0;
}
.tourism-award {
  position: relative;
  height: 335px;
  width: 213px;
  cursor: pointer;
  margin: 40px 40px 40px 40px;
}
.tourism-award-title {
  position: absolute;
  bottom: 0;
  height: 30%;
  width: 100%;
  background-color: #BD9A35;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: white;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 3px;
}
.tourism-award-title p {
  line-height: 10px;
}
.tourism-award-pic {
  position: absolute;
  top: 0;
  height: 70%;
  width: 100%;
  background-color: white;
}

