#tourism-looking-for-help {
  position: relative;
}


@media screen and (max-width: 920px) {

  .tourism-work {
    height: 24vw;
    margin: 0;
    outline: 0;
  }

  .tourism-clients-clients {
    position: relative;
    height: calc(24vw * 4);
    width: calc(24vw * 2.1);
    cursor: pointer;
    margin: 100px 0 100px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
  }

}

@media screen and (max-width: 425px) {

  .tourism-experts {
    position: relative;
    background-color: white;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .tourism-industry-experts {
    position: relative;
    top: 0;
    right: 0;
    font-size: 25px;
    color: #BD9A35;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 3px;
    line-height: 35px;
    font-family: 'Rogan-SemiBold', sans-serif;
    margin: 50px 0 50px 0;
  }

  .tourism-industry-info {
    position: relative;
    left: 0;
    color: #727272;
    width: 85%;
    height: auto;
    font-family: 'Rogan-Light', sans-serif;
    font-size: 18px;
    letter-spacing: 1px;
    line-height: 32px;
    margin: 0 0 100px 0;
    text-align: center;
  }

  .tourism-work {
    height: 60vw;
    margin: 0;
    outline: 0;
  }

  .tourism-clients-clients {
    position: relative;
    height: calc(60vw * 8);
    width: calc(60vw * 1.1);
    cursor: pointer;
    margin: 100px 0 100px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

}